<script>
import Square from './Square.svelte';
	
let imageName = 'plane';
let show = new Array(9).fill('');
	
const handleClick = (i) => {
	if (show[i] === '') {
		show[i] = imageName;
	} else {
		return;
	}
	imageName = imageName === 'plane' ? 'firetruck' : 'plane';	
};

function refresh() {
	show = new Array(9).fill('');
}
</script>

<h1>
	Simple <span class="title">Tic Tac Toe</span>
</h1>
<p>
	There is no AI so you need to:
</p>
<ol>
	<li>Choose who is going to be the plane and who is going to be the firetruck</li>
	<li>Decide when the game is finished<br /> - a win or tie</li>
	<li>Keep score</li>
</ol>

<table>
	<tbody>
		<tr><td><Square on:click={(evt) => handleClick(0)}  image={show[0]} /></td>
			<td><Square on:click={(evt) => handleClick(1)} image={show[1]} /></td>
			<td><Square on:click={(evt) => handleClick(2)} image={show[2]} /></td>
		</tr>
		<tr><td><Square on:click={(evt) => handleClick(3)} image={show[3]} /></td>
			<td><Square on:click={(evt) => handleClick(4)} image={show[4]} /></td>
			<td><Square on:click={(evt) => handleClick(5)} image={show[5]} /></td>
		</tr>
		<tr><td><Square on:click={(evt) => handleClick(6)} image={show[6]} /></td>
			<td><Square on:click={(evt) => handleClick(7)} image={show[7]} /></td>
			<td><Square on:click={(evt) => handleClick(8)} image={show[8]} /></td>
		</tr>		
	</tbody>
</table>
<button on:click={refresh}>
	New Game
</button>
