<script>
	export let image = '';	
	
	const mouseDown = (evt) => {
		evt.target.style.backgroundColor = '#f6f6f6';
	};
	
	const mouseUp = (evt) => {
		evt.target.style.backgroundColor = '#fff';
	};
	</script>
<style>
	.plane-url, .firetruck-url {
		background-position: center center;
		background-repeat: no-repeat;
		background-size: 80px 80px;		
	}
	.plane-url {
		background-image: url('./images/plane.png');			
		/* https://www.stickees.com/files/animals/twitter/934-twitter-plane-brown-sticker.png */
	}
	.firetruck-url {
		background-image: url('./images/firetruck.png');	
		/* https://www.shareicon.net/data/128x128/2015/07/10/67034_fire_512x512.png */	
	}
	div {
		background-color: #fff;
		width: 100px;
		height: 100px;	
		padding: 0;
	}	
</style>
<div class="{image === 'plane' ? 'plane-url' : image === 'firetruck' ? 'firetruck-url' : ''}" on:click on:mouseover={mouseDown} on:mouseout={mouseUp}>	
</div>